body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;


  overflow: hidden;
  -webkit-overflow-scrolling: touch;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* ================================================图片动画效果==================================  start*/

/* 圆形 */
.anim-circle {
  animation: clipCircleIn 1.5s .4s;
}

@keyframes clipCircleIn {
  30% {
    clip-path: circle(0);
  }

  60% {
    clip-path: circle(60%);
  }

  100% {
    clip-path: circle(90%);
  }
}

/* 三角形 */
.anim-triangle {
  animation: clipTriangleIn 1.5s;
}

@keyframes clipTriangleIn {
  30% {
    clip-path: polygon(50% 50%, 49% 51%, 51% 51%);
  }

  100% {
    clip-path: polygon(50% -100%, -100% 150%, 200% 150%);
  }
}

/* 圆角 */
.anim-radius {
  animation: clipRadiusIn 1.5s;
}

@keyframes clipRadiusIn {
  30% {
    clip-path: inset(50% round 10% 50%);
  }

  100% {
    clip-path: inset(0% round 0);
  }
}

/* 菱形 */
.anim-diamond {
  animation: clipDiamond 1.5s;
}

@keyframes clipDiamond {
  30% {
    clip-path: polygon(50% 50%, 50% 50%, 50% 50%, 50% 50%);
  }

  100% {
    clip-path: polygon(50% -50%, 150% 50%, 50% 150%, -50% 50%);
  }
}

/* fadeLeft  从左侧渐入 */
.anim-fade-left {
  animation: fadeLeft 1.5s;
}

@keyframes fadeLeft {
  0% {
    /* width: 0;
    height: 0; */
    width: 1.3333vw;
    height: 0.6667vh;
    transform: translateX(-600px);
  }
}

/* 从左侧滑入*/
.anim-fade-left-no {
  animation: fadeLeftNO 2s;
}

@keyframes fadeLeftNO {
  0% {
    width: 0;
    transform: translateX(-200px);
  }

  50% {
    width: 0;
    transform: translateX(-100px);
  }
}

/* fadeRight  从右侧滑入 */
.anim-fade-right {
  animation: fadeRight 1.5s;
}

@keyframes fadeRight {
  0% {
    width: 1.3333vw;
    height: 0.6667vh;
    transform: translateX(600px);
  }
}

/* fadeRight  从右侧渐入 */
.anim-fade-right-no {
  animation: fadeRightNO 1.5s;
}

@keyframes fadeRightNO {
  0% {
    transform: translateX(600px);
  }
}

/* 从上方滑入 */
.anim-fade-down {
  animation: fadeDown 1.5s linear;
}

@keyframes fadeDown {
  from {
    height: 0;
    transform: translateY(-600px);
  }
}

/* 十字星cross到矩形 square */
.anim-cross-to-square {
  animation: crossToSquare 1.5s;
}

@keyframes crossToSquare {
  0% {
    clip-path: polygon(50% 20%, 50% 50%, 20% 50%, 50% 50%, 50% 80%, 50% 50%, 80% 50%, 50% 50%);
  }

  100% {
    clip-path: polygon(50% 0%, 0% 0%, 0% 50%, 0% 100%, 50% 100%, 100% 100%, 100% 50%, 100% 0%);
  }
}

/* 扇形展开效果 */
.anim-fan-out {
  animation: fanOut 1.5s linear;
}

@keyframes fanOut {
  0% {
    clip-path: polygon(50% 100%, 50% 0%, 0% 0%, 100% 0%, 50% 0%);
  }

  50% {
    clip-path: polygon(50% 100%, 0% 0%, 0% 0%, 100% 0%, 100% 0%);
  }

  100% {
    clip-path: polygon(50% 100%, 0% 100%, 0% 0%, 100% 0%, 100% 100%);
  }
}

/* 淡入 */
.anim-fade-in {
  animation: fadeIn 1.5s linear;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    display: block;
  }

  100% {
    opacity: 1;
  }
}

/*五角星 ☆ 作为遮罩背景图实现动画 */
.anim-fade-star {
  mask: url(./assets/star.svg) no-repeat center;
  mask-size: 300% 300%;
  /* Safari没有这个类似“初始化”的东西居然无效 */
  animation: fadeStar 1.5s .5s both linear;
}

@keyframes fadeStar {
  from {
    mask-size: 0 0;
  }

  to {
    mask-size: 300% 300%;
  }
}


/* 爱心 ❤ 作为遮罩背景图实现动画 */
.anim-fade-heart {
  mask: url(./assets/heart.svg) no-repeat center;
  mask-size: 200% 300%;
  /* Safari没有这个类似“初始化”的东西居然无效 */
  animation: fadeHeart 1.5s .5s both linear;
}

@keyframes fadeHeart {
  from {
    mask-size: 0 0;
  }

  to {
    mask-size: 200% 300%;
  }
}

/* ================================================图片动画效果==================================  end */


/* =========================================文字效果=========================================  start */

/* 楷体文字 */
.text-kaiti {
  font-family: KaiTi, KaiTi_GB2312;
}


/* 自动打字效果 */
.text-auto-type {
  display: inline-block;
  width: 16em;
  overflow: hidden;
  white-space: nowrap;
  animation: typing 1.5s steps(20);
}

@keyframes typing {
  from {
    width: 0;
  }
}

/* ========================================文字效果========================================  end */