.App{
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  max-width: 680px;
  position: absolute;
  display: inline-block;
  transform: translate(-50%, -50%);
}

.Box{
  top: 0;
  width: 100%;
  height: 100%;
  position: absolute;
}

.item{
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  transition: all .4s;
  flex-direction: column;
  justify-content: space-between;
  transform: translate(-50%, -50%);
}