@keyframes sixfadeNo {
  0% {
    width: 0;
  }

  100% {
    width: 0;
  }
}

@keyframes sixTextfadeNo {
  0% {
    width: 0;
    font-size: 0;
  }

  100% {
    width: 0;
    font-size: 0;
  }
}

/* 左侧阴影向右滑 */
.six.anim-fade-left {
  animation: sixfadeNo 1.5s, sixfadeLeft 1.5s 1s;
}

@keyframes sixfadeLeft {
  0% {
    width: 0;
    transform: translateX(-800px);
  }

  100% {
    width: 50%;
  }
}




/* 图片向右滑 */
.six.photo.anim-fade-right {
  animation: sixfadeNo 2s, sixphotoFadeRight 1.5s 2s;
}

@keyframes sixphotoFadeRight {
  0% {
    transform: translateX(800px);
  }
}

/* 文字向左滑 */
.six.text.anim-fade-left {
  animation: sixTextfadeNo 2s, sixphotoFadeLeft 1.5s 2s;
}

/* 图片向左滑 */
.six.photo.anim-fade-left {
  animation: sixfadeNo 3s, sixphotoFadeLeft 1.5s 3s;
}

@keyframes sixphotoFadeLeft {
  0% {
    transform: translateX(-800px);
  }
}

/* 文字向右滑 */
.six.text.anim-fade-right {
  animation: sixTextfadeNo 3s, sixphotoFadeRight 1.5s 3s;
}


.left-bottom-text-wrapper {
  position: absolute;
  left: 0;
  right: 76vw;
  bottom: 0;
  top: 55%;
  writing-mode: vertical-rl;
  letter-spacing: 2px;
}

.left-bottom-text-wrapper .left-bottom-text-title {
  left: 0;
  font-size: 6vw;
  position: absolute;
  top: 3vh;
}

.left-bottom-text-wrapper .left-bottom-text-desc {
  position: absolute;
  left: 8vw;
  top: 9vh;
}
