.Bgm{
    top: 1vw;
    right: 1vw;
    width: 14vw;
    opacity: 0.8;
    z-index: 100;
    height: 14vw;
    border-radius: 50%;
    position: absolute;
    background-size: contain;
    background-position: center;
}

.open{
    animation:mymove 5s linear infinite;
}

@keyframes mymove
{
    from {transform:rotate(0deg);}
    to {transform:rotate(360deg);}
}